<template>
	<Modal :title="title" @closed="$emit('closed')">
		<UdfRuntime :id="id" :runtime="data" :version="version"></UdfRuntime>
	</Modal>
</template>

<script>
import Modal from './Modal.vue';
import UdfRuntime from '@openeo/vue-components/components/UdfRuntime.vue';

export default {
	name: 'UdfRuntimeModal',
	components: {
		Modal,
		UdfRuntime
	},
	props: {
		id: {
			type: String
		},
		data: {
			type: Object
		},
		version: {
			type: String
		}
	},
	computed: {
		title() {
			return this.data.title || this.id;
		}
	}
}
</script>

<style>
.udf-runtime h2 {
	display: none;
}
</style>